.formContainer {
  background-color: rgb(255, 255, 255);
  display: flex;
  width: 40%;
  height: 100%;
  border-radius: 0px 10px 10px 0px;
  overflow-y: scroll;
  border-right: 1px solid rgb(239, 239, 239);
  flex-direction: column;
}

.formContainer::-webkit-scrollbar {
  display: none;
}
